@import 'mixin';
@import "color-size";

header {
  padding: 1.5rem 0 0;
  overflow: hidden;
  text-align: center;
  font-size: $size-15;
  a {
    display: inline-block;
    h2 {
      display: inline-block;
      margin: 0;
      font-size: $size-15;
    }
    &:hover {
      color: $soft-blue;
    }
  }
  .icon-phone:before {
    font-size: $size-14;
    margin: 0;
    @include transition(color .3s);
  }
  .icon-phone {
    &:hover {
      &:before {
        color: lighten($black, 35%);
      }
    }
  }
  .img-logo {
    display: block;
    max-width: 250px;
  }
}
.first-part {
  .logo {
    position: relative;
    background: url("../img/front1.jpg") no-repeat 0 75%;
    background-size: 100%;
    height: 26rem;
    overflow: hidden;
    .inner-logo {
      position: absolute;
      top: 5%;
      width: 100%;
      color: $white;
      text-align: center;
      a {
        display: block;
        color: inherit;
        h1 {
          margin: 0 0 1.2rem 0;
          font-size: $size-45;
        }
      }
      .btn {
        margin: 0;
        background-color: $soft-blue;
        @include transition(background-color .3s);
        &:hover {
          background-color: lighten($soft-blue, 10%);
          @include transition(background-color .3s);
        }
        a {
          display: block;
          padding: 2rem 1rem;
          font-size: $size-20;
          color: $white;
        }
      }
      h2 {
        margin: 0.5rem 1rem 0;
      }
    }
  }
}

.btn-perso {
  float: inherit !important;
  span {
    background-color: $black;
    @include transition(all .3s);
    &:before,
    &:after {
      content: '';
      position: absolute;
      width: 100%;
      height: 0.2rem;
      top: 1rem;
      left: 0;
      background-color: $black;
      @include transition(all .3s);
    }
    &:after {
      content: '';
      top: 2rem;
    }
  }
}
[data-main="phone-tab"], [data-main="desk"] {
  width: 100%;
  height: 0;
  margin: 0;
  /*opacity: 0;*/
  visibility: hidden;
  overflow: hidden;
  text-align: center;
  font-size: $size-14;
  @include transition(all .3s);
  ul {
    li {
      box-shadow: none;
      @include transition(all .2s);
      &:hover {
        @include transition(all .2s);
        opacity: 0.6;
        background-color: $soft-blue;
        box-shadow: 0 10px 10px -5px;
        a {
          color: $white;
        }
      }
      a {
        display: block;
        height: 0;
        text-transform: uppercase;
        @include transition(all .2s);
      }
    }
  }
}


