@import "mixin";
@import "color-size";


@media (min-width: 992px) {
.logo {
  background-position: 50% 70%;
  background-size: 80%;
  max-width: 100rem;
  max-height: 40rem;
  margin: 0 auto;
}
  [data-main="desk"] {
    display: block;
    width: inherit;
    font-size: $size-18;
    height: inherit;
    margin: 3rem 0 0;
    opacity: 1;
    visibility: inherit;
    text-align: center;
    ul {
      margin: 0;
      li {
        display: inline-block;
        padding: 0 1rem;
        height: 5rem;
        line-height: 5rem;
        border-bottom: 0;
        @include transition(border-bottom 0.2s linear);
        @include transition(all .3s);
        &:hover {
          @include transition(all .3s);
          opacity: 1;
          color: $white;
          background-color: $soft-blue;
          border-bottom: 5px solid $white;
        }
        a {
          height: inherit;
          text-transform: capitalize;
        }
      }
    }
  }
  .btn-perso {
    margin: 1.5rem 3rem 0 0;
  }
}
.second-part {
  font-size: $size-16;
  .wrap-text {
    h2 {
      font-size: $size-35;
    }
    p.presentation {
      width: 50%;
    }
    .presentation {
      text-align: center;
      ul {
        margin: 0 0 0 1.5rem;
        text-align: justify;
      }
    }
  }
  .wrap-info {
    .inner-info {
      .box {
        min-height: 30rem;
      }
      h3 {
        text-align: center;
      }
      img {
        margin: 1rem auto 0;
      }
    }
  }
}
footer {
  h1 {
    font-size: $size-16;
  }
}