@import "mixin";

footer {
  border-top: 1px solid lighten($black, 10%);
  background-color: $black;
  color: $white;
  padding: 1.5rem 1rem;
  text-align: center;
  font-size: $size-12;
  overflow: hidden;
  h2 {
    display: block;
    font-size: $size-13;
    color: $soft-blue;
    &:hover {
      color: $white;
    }
  }
  a {
    display: inline-block;
    color: $white;
    &:hover {
      color: $soft-blue;
    }
  }
}