@import "mixin";
@import "color-size";


.second-part {
  font-size: $size-13;
  overflow: hidden;
  .wrap-text {
    background-color: darken($white, 7%);
    padding: 1.5rem 0;
    text-align: center;
    h2 {
      margin: 0 0 2rem 0;
      font-size: $size-25;
      font-weight: 600;
      text-transform: uppercase;
    }
    p.presentation {
      width: 70%;
      &:first-line{
        margin: 0 0 0 1rem;
      }
      margin: 1rem auto 0;
      .pres {
        display: inline-block;
        margin: 1rem 0;
        font-size: $size-15;
      }
      ul {
        display: inline-block;
        vertical-align: text-top;
        text-align: justify;
      }
      .icon-checkmark:before {
        margin: 0 .3rem 0 0;
        color: $soft-blue;

      }
    }
    p.presentation {
      &:nth-last-child(2) {
        strong {
          display: block;
          margin: 0 0 1rem 0;
          text-decoration: underline;
        }
        a {
          display: block;
          width: 13rem;
          margin: .7rem auto 0;
          font-weight: bold;
          @include transition(color .3s);
          &:hover {
            color: $soft-blue;
            @include transition(color .3s);
          }
        }
      }
    }
  }
  .wrap-form,
  .wrap-info,
  .wrap-tarif {
    position: relative;
    overflow: hidden;
    .wrap-title {
      background-color: $black;
      h2 {
        display: inline-block;
        margin: 1rem 0 1rem .5rem;
        text-transform: uppercase;
        font-size: $size-20;
        color: $soft-blue;
        cursor: pointer;
      }
      .btn-perso {
        width: $size-20;
        margin: 1rem .5rem 0 0;
        span {
          top: 1rem;
          background-color: $white;
          &:after {
            content: '';
            position: absolute;
            width: 100%;
            height: 0.2rem;
            top: 0;
            left: 0;
            background-color:$white;
            @include transition(all .3s);
            @include transform(rotate(90deg));
          }
        }
      }
    }
    .inner-form,
    .inner-info,
    .inner-tarif {
      visibility: hidden;
      overflow: hidden;
      height: 0;
      padding: 0;
      opacity: 0;
      @include transition(all .3s);
    }
    .inner-form {
      a,
      p {
        display: block;
        font-size: 1.7rem;
        text-align: center;
      }
      p {
        margin: 1rem 0 0 0;
      }
      h3 {
        text-align: center;
      }
      form {
        padding: 1rem 0;
        i {
          color: $red;
        }
        fieldset {
          margin: 1rem 0 0 0;
        }
        p:first-child {
          font-size: 1.1rem;
        }
        p {
          font-size: $size-13;
          padding: 1rem 0;
          margin: 0;
          text-align: inherit;
        }
        input {
          width: 100%;
          height: 3rem;
          padding: 0 0.5rem;
          background-color: $white;
          color: darken($soft-blue, 10%);
          font-size: $size-15;
          border: 1px solid lighten($soft-blue, 10%);
        }
        textarea {
          width: 100%;
          padding: 0 1rem;
        }
        .error-hidden-name,
        .error-hidden-prenom,
        .error-hidden-mail,
        .error-hidden-tel,
        .error-hidden-depart,
        .error-hidden-date,
        .error-hidden-time,
        .error-hidden-arrive,
        .error-hidden-comment {
          height: 0;
          padding: 0;
          visibility: hidden;
          overflow: hidden;
          opacity: 0;
          color: red;
          @include transition(all .3s);
        }

// ajout form
        .error-visible {
          height: inherit;
          visibility: visible;
          opacity: 1;
          @include transition(all .3s);
        }
//fin ajout

        button {
          display: block;
          background-color: $soft-blue;
          color: $white;
          font-size: $size-12;
          padding: 2rem 5rem;
          margin: 3rem auto 0;
          text-transform: uppercase;
          &:hover {
            background-color: lighten($soft-blue, 15%);
            @include transition(background-color .3s);

          }
        }
      }
    }

// ajout
    .open-form,
    .open-info {
      height: inherit;
      visibility: visible;
      opacity: 1;
      @include transition(all .3s);
    }
    .open-form {
      padding: 1.5rem 0;
    }
//fin ajout
  }
//ajout btn form
  .btn-form {
    span {
      &:after {
        opacity: 0;
        @include transition(opacity .3s);
      }
    }
  }
//fin ajout

  .wrap-info {
    .inner-info {
      .box {
        padding: 2rem 1.5rem;
        font-size: 1.4rem;
        &:nth-child(1),
        &:nth-child(4) {
          background-color: inherit;
        }
        &:nth-child(2n+1) {
          background-color: darken($white, 9%);
        }
      }
      h3 {
        margin: 0 0 2rem 0;
        font-size: $size-20;
        text-transform: capitalize;
        i {
          display: inline-block;
        }
        .icon-briefcase:before,
        .icon-hour-glass:before,
        .icon-info:before {
          margin: 0 .5rem 0 0;
          font-size: 2.5rem;
          vertical-align: top;
        }
      }
      p {
        line-height: 1.5;
        font-size: $size-14;
      }
      img {
        width: 10rem;
        border: 1px solid $black;
        @include transition(all .3s);
        &:hover {
          border-color: $soft-blue;
          @include transform(scale(1.5));
          @include transition(all .3s);
        }
      }
    }
  }
  .wrap-tarif {
    .inner-tarif {
      font-size: $size-14;
      background-color: darken($white, 9%);
      table {
        display: block;
        padding: 2rem 0 1rem 0;
        tbody {
          display: table;
          width: 100%;
          background-color: $soft-blue;
          border: .3rem solid darken($white, 9%);
          color: $white;
          tr:first-child {
            font-weight: 600;
          }
          td {
            width: 20%;
            height: 4rem;
            border: .2rem solid $black;
            padding: 0.5rem 0;
            font-size: 1.4rem;
            text-align: center;
          }
          .first-col {
            padding: 0;
            text-align: inherit;
            text-transform: capitalize;
            font-weight: 600;
          }
          .empty {
            background-color: $black;
          }
        }
      }
      p:before {
        content: '*';
        margin: 0 0.5rem 0 0;
        i {
          display: inline-block;
        }
      }
      .icon-cc-visa:before,
      .icon-cc-amex:before,
      .icon-banknote:before {
        font-size: 1.8rem;
        color: $lime-green;
      }
      .icon-cc-visa:before{
        color: $blue;
      }
      .icon-cc-amex:before {
        color: $light-blue;
      }
      h4 {
        font-size: $size-16;
        font-weight: 600;
        margin: 2rem 0;
        text-align: center;
        overflow: hidden;
        clear: both;
      }
    }

    //ajout tarif

    .btn-tarif {
      span {
        &:after {
          opacity: 0;
          @include transition(opacity .3s);
        }
      }
    }
    .open-tarif {
      height: inherit;
      padding: 0;
      opacity: 1;
      visibility: visible;
      @include transition(all .3s);
    }

    //fin  tarif
  }
}