@import "mixin";
@import "color-size";


.second-part {
  .box {
    height: 40rem;
    padding: 2rem 1.5rem;
    &:nth-child(1),
    &:nth-child(4){
      background-color: inherit;
    }
    &:nth-child(2n+1) {
      background-color: darken($white, 9%);
    }
  }
  h3 {
    margin: 0 0 2rem 0;
    font-size: $size-20;
    text-align: center;
    text-transform: capitalize;
    i{
      display: inline-block;
    }
    .icon-hour-glass:before,
    .icon-clock:before,
    .icon-compass:before,
    .icon-briefcase:before {
      margin: 0 0.5rem 0 0;
      font-size: $size-25;
      vertical-align: top;
    }
  }
  p {
    margin: 0 auto 2rem;
    text-align: left;
    font-size: $size-14;
    line-height: $size-25;
  }
}