@import "color-size";

@media (min-width: 768px) {
  .head-wrap {
    a {
      font-size: $size-13;
      text-decoration: underline;
      h2 {
        text-decoration: underline;
      }
    }
    .head-contact {
      font-size: $size-22;
      p {
        font-size: $size-15;
      }
    }
  }
  .logo {
    background-position: 0 75%;
    background-size: cover;
    height: 50rem;
    .inner-logo {
      top: 35%;
      h1 {
        margin: 0 0 1.5rem 0;
      }
      h2 {
        margin: 2rem 0;
      }
    }
  }
  .main-wrap {
    nav {
      font-size: $size-16;
    }
    /*.btn-perso {
      margin: 1.5rem 2rem 0 0;
    }*/
  }
  .second-part {
    font-size: $size-14;
    .wrap-form {
      .inner-form {
        form {
          input {
            margin: .5rem 0;
          }
        }
      }
    }
    .wrap-text {
      h2 {
        font-size: $size-30;
      }
      .presentation {
        font-size: $size-14;
        ul {
          width: 75%;
        }
      }
      p.presentation {
        width: 70%;
        &:nth-last-child(2) {
          strong {
            display: initial;
          }
        }
      }
      div.presentation {
        width: 85%;
      }
    }
    .wrap-info {
      .inner-info {
        .box {
          padding: 2% 1%;
        }
        h3 {
          font-size: $size-23;
          margin: 0 auto 2rem;
          .icon-briefcase:before,
          .icon-hour-glass:before {
            margin: 0 0.8rem 0 0;
          }
        }
        p {
          margin: 0 auto;
        }
      }
    }
  }
  footer {
    font-size: $size-13;
    h1 {
      font-size: $size-15;
    }
  }
}